    font-size: 1em;
    text-align: center;
    text-decoration: none;
    border-radius: .25em;
    border: 1px solid #ccc;
    background: #fcfcfc;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
